body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #1e1e1a;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

#blank {
  width: 70%;
  max-height: 675px;
  background-color: #fff;
  padding: 10px 0px;
  margin: 84px auto;
  border-radius: 4px;
}

.recipe {
  display: block;
  list-style: none;
  width: 90%;
  height: 80px;
  background-color: #1e1e1a;
  margin: 0 auto;
  border-radius: 10px;
  position: relative;
}

.add-recipe-btn {
  width: 105px;
  height: 40px;
  border-radius: 5px;
  background-color: #f1c40f;
  border: none;
  color: #1e1e1a;
  cursor: pointer;
  margin-left: 20px;
  font-size: 16px;
  font-family: Lato;
  margin-top: 20px;
  margin-bottom: 5px;
}

.recipe-name {
  font-family: Lato;
  color: #ffffff;
  margin-left: 15px;
  font-size: 22px;
  font-weight: normal;
  padding-top: 10px;
}

#dialog {
  width: 100%;
  position: absolute;
  height: 100%;
  background-color: rgba(0,0,0, 0.5);
  z-index: 20;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.modal-window {
  width: 600px;
  height: 266px;
  background-color: #fff;
  border-radius: 4px;
  margin: 0px auto;
  margin-top: -93px;
}

.modal-window .add-recipe-btn {
  margin-left: 0px;
  margin-top: 20px;
}

.close-modal {
  width: 35px;
  height: 35px;
  background-color: #f22933;
  font-size: 20px;
  color: #ffffff;
  cursor: pointer;
  border: none;
  border-radius: 0px 4px 0px 0px;
  position: relative;
  left: calc(100% - 35px);
}

.visuallyhidden {
   border: 0;
   clip: rect(0 0 0 0);
   height: 1px;
   margin: -1px;
   overflow: hidden;
   padding: 0;
   position: absolute;
   width: 1px;
}

.modal-headline {
  margin-top: -25px;
  font-family: Lato;
  font-weight: normal;
  color: #1e1e1a;
}

.modal-input {
  width: 330px;
  height: 40px;
  border: 1px solid #7f8c8d;
  border-radius: 5px;
  margin-top: 15px;
  padding-left: 5px;
  font-family: Lato;
  font-weight: normal;
  font-size: 16px;
  color: #1e1e1a;
}

.modal-input::-webkit-input-placeholder {
  font-family: Lato;
  font-weight: normal;
  color: #7f8c8d;
}

.modal-form {
  margin-top: -15px;
  margin-left: 35px;
}

.ingredients-list {
  position: absolute;
  margin-top: -30px;
  margin-left: 35px;
}

.ingredients-list li {
  display: inline-block;
  list-style: none;
  margin-top: 4px;
  margin-left: 4px;
}

.ingredients-list li:first-child {
  margin-left: 12px;
}

.ingredients-list li::after {
  content: ','
}

.ingredients-list li:last-child::after {
  content: '.'
}

.ingredients-list li, .ingredients-list__headline {
  font-family: Lato;
  font-style: italic;
  color: #7f8c8d;
  font-size: 14px;
}

.ingredients-list__headline {
  position: absolute;
  margin-left: 15px;
  margin-top: -26px;
}

.remove-recipe {
  width: 25px;
  height: 25px;
  background-color: transparent;
  position: absolute;
  top: 35%;
  right: 0%;
  margin-right: 13px;
  border: none;
  color: #ffffff;
  cursor: pointer;
  font-size: 18px;
}

.fallback__icon {
  text-align: center;
  margin-top: 30px
}

.fallback__paragraph {
  margin-top: 5px;
  text-align: center;
  font-family: Lato;
  font-size: 20px;
  color: #7f8c8d;
}

.logo-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  position: relative;
  top: 50px;
}

.logo-wrapper__headline {
  font-size: 32px;
  color: #f1c40f;
  font-family: Roboto Slab;
  margin-left: 25px;
}

.blank__content {
  max-height: 500px;
  overflow-y: auto;
}

@media (max-width: 860px) {
  .logo-wrapper {
    top: 5px;
  }

  #blank {
    width: 100%;
    border-radius: 0px;
    margin-top: 10px;
  }
}

@media (max-width: 600px) {
  .modal-window  {
    width: 100%;
    border-radius: 0px;
  }

  .close-modal {
    border-radius: 0px;
  }
}

@media (max-width: 445px) {
  .logo-wrapper {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }

  #blank {
    margin-top: 0px;
  }

  .logo-wrapper__headline {
    margin-left: 0px;
    margin-top: 0px;
  }
}

@media (max-width: 445px) {
  .logo-wrapper__headline {
    display: none;
  }

  #blank {
    margin-top: 15px;
    max-height: 464px;
  }

  .blank__content {
    max-height: 380px;
  }
}

@media (max-width: 400px) {
  .modal-form {
    margin-left: 5px;
  }

  .modal-input {
    width: 310px;
  }
}

@media (max-width: 320px) {
  #blank {
    max-height: 464px;
  }

  .blank__content {
    max-height: 380px;
  }
}



